import { CloudApiGateway } from '@/apps/console/apis/cloud/gateway';
import {
  Execution,
  ExecutionDTO,
  ExecutionLogEntryDTO,
  ExecutionLogs,
  ExecutionRepository,
  GetExecutionTasksResponse,
  ListExecutionResponse,
  ListExecutionsParams,
} from './types';

export class RemoteExecutionRepository implements ExecutionRepository {
  async list({
    projectId,
    ...request
  }: ListExecutionsParams): Promise<{ executions: Execution[]; totalCount: number }> {
    const query = {
      ...request,
      offset: request.offset?.toString(),
      limit: request.limit?.toString(),
    };

    Object.keys(query).forEach(
      (key) =>
        query[key as keyof typeof query] === undefined && delete query[key as keyof typeof query],
    );

    const response = (await CloudApiGateway.get(
      `projects/${projectId}/executions`,
      query,
    )) as ListExecutionResponse;

    return {
      executions: response.executions.map((dto) => Execution.from(dto)),
      totalCount: response.totalCount,
    };
  }

  async fetchLogs(projectId: string, executionId: string): Promise<ExecutionLogs> {
    const response = (await CloudApiGateway.get(
      `projects/${projectId}/executions/${executionId}/logs`,
    )) as ExecutionLogEntryDTO[];
    return ExecutionLogs.from(response);
  }

  async fetchThreadData(projectId: string, executionId: string): Promise<Record<string, string>> {
    const result = await CloudApiGateway.get(
      `projects/${projectId}/executions/${executionId}/thread-data`,
    );

    return result.response as Record<string, string>;
  }

  async getExecutionTasks(
    projectId: string,
    executionId: string,
  ): Promise<GetExecutionTasksResponse> {
    return (await CloudApiGateway.get(
      `projects/${projectId}/executions/${executionId}/tasks`,
    )) as GetExecutionTasksResponse;
  }

  async fetchStages(): Promise<{ id: string; title: string }[]> {
    throw new Error('Method not implemented.');
  }
}

export class LocalExecutionRepository implements ExecutionRepository {
  async list(
    request: ListExecutionsParams,
  ): Promise<{ executions: Execution[]; totalCount: number }> {
    const query = {
      ...request,
      offset: request.offset?.toString(),
      limit: request.limit?.toString(),
    };

    Object.keys(query).forEach(
      (key) =>
        query[key as keyof typeof query] === undefined && delete query[key as keyof typeof query],
    );

    const filteredQuery = Object.fromEntries(
      Object.entries(query ?? {}).filter(([, value]) => value !== undefined && value !== null),
    ) as Record<string, string>;

    const queryString =
      Object.keys(filteredQuery).length > 0
        ? `?${new URLSearchParams(filteredQuery).toString()}`
        : '';

    const res = await fetch(`/_editor/api/executions${queryString}`);
    const response = (await res.json()) as { executions: ExecutionDTO[]; totalCount: number };
    return {
      executions: response.executions.map((dto) => Execution.from(dto)),
      totalCount: response.totalCount,
    };
  }

  async fetchLogs(_projectId: string, executionId: string): Promise<ExecutionLogs> {
    const res = await fetch(`/_editor/api/logs/${executionId}`);
    const logs = (await res.json()) as ExecutionLogEntryDTO[];
    return ExecutionLogs.from(logs);
  }

  async fetchThreadData(): Promise<Record<string, string>> {
    return {};
  }

  async getExecutionTasks(
    _projectId: string,
    executionId: string,
  ): Promise<GetExecutionTasksResponse> {
    const res = await fetch(`/_editor/api/executions/${executionId}/tasks`);
    return (await res.json()) as GetExecutionTasksResponse;
  }

  async fetchStages(): Promise<{ id: string; title: string }[]> {
    const res = await fetch(`/_editor/api/executions/stages`);
    return (await res.json()) as { title: string; id: string }[];
  }
}
