import { TaskDTO } from '@/services/tasks/tasksController';

type DeprecatedEventType = 'form-message' | 'unhandled-exception';
type EventType = 'stderr' | 'stdout';

export type ExecutionLogEntryDTO = {
  executionId: string;
  createdAt: string;
  event: DeprecatedEventType | EventType;
  payload: Record<string, unknown>;
  sequence: number;
};

export const executionStatuses = [
  'running',
  'lock-failed', // deprecated in abstra v3
  'failed',
  'finished',
  'abandoned',
] as const;
export type ExecutionStatusType = (typeof executionStatuses)[number];

type Request = {
  queryParams: Record<string, string>;
  headers: Record<string, string>;
  method: string;
  body: string;
};

type Response = {
  headers: Record<string, string>;
  status: number;
  body: string;
};

type HookContext = {
  request: Request;
  response: Response;
  sentTasks: string[];
  legacyThreadData: Record<string, string>;
  mockExecution: unknown;
};

type FormContext = {
  request: Request;
  sentTasks: string[];
  legacyThreadData: Record<string, string>;
  mockExecution: unknown;
};

type ScriptContext = {
  taskId: string;
  sentTasks: string[];
  legacyThreadData: Record<string, string>;
  mockExecution: unknown;
};

type JobContext = {
  sentTasks: string[];
  legacyThreadData: Record<string, string>;
  mockExecution: unknown;
};

export type ClientContext = HookContext | FormContext | ScriptContext | JobContext;

export type ExecutionDTO = {
  id: string;
  createdAt: string;
  updatedAt: string;
  status: ExecutionStatusType;
  context: ClientContext;
  buildId: string;
  stageId: string;
  stageRunId: string | null;
  workerId: string;
  appId: string;
  projectId: string;
};

export type ListExecutionsParams = {
  buildId?: string;
  stageId?: string;
  status?: ExecutionStatusType;
  projectId?: string;
  offset?: number;
  limit?: number;
  startDate?: string;
  endDate?: string;
  search?: string;
};

export type ListExecutionResponse = {
  executions: ExecutionDTO[];
  totalCount: number;
};

export class ExecutionLogs {
  private constructor(private readonly dto: ExecutionLogEntryDTO[]) {}

  static from(dto: ExecutionLogEntryDTO[]): ExecutionLogs {
    return new ExecutionLogs(dto);
  }

  get entries(): ExecutionLogEntryDTO[] {
    return this.dto
      .sort((a, b) => a.sequence - b.sequence)
      .filter((entry) => entry.event !== 'form-message');
  }
}

export class Execution {
  private constructor(private readonly dto: ExecutionDTO) {}

  static from(dto: ExecutionDTO): Execution {
    return new Execution(dto);
  }

  get id(): string {
    return this.dto.id;
  }

  get shortId(): string {
    return this.dto.id.slice(0, 8);
  }

  get createdAt(): Date {
    return new Date(this.dto.createdAt);
  }

  get updatedAt(): Date {
    return new Date(this.dto.updatedAt);
  }

  get status(): ExecutionDTO['status'] {
    return this.dto.status;
  }

  get context(): ExecutionDTO['context'] {
    return this.dto.context;
  }

  get buildId(): ExecutionDTO['buildId'] {
    return this.dto.buildId ?? '';
  }

  get stageId(): ExecutionDTO['stageId'] {
    return this.dto.stageId;
  }

  get duration_seconds() {
    if (this.status === 'running') return '-';
    return `${(this.updatedAt.getTime() - this.createdAt.getTime()) / 1000} s`;
  }

  get stageRunId(): ExecutionDTO['stageRunId'] {
    return this.dto.stageRunId;
  }

  get projectId(): ExecutionDTO['projectId'] {
    return this.dto.projectId;
  }
}

export type GetExecutionTasksResponse = {
  triggerTask: TaskDTO | null;
  sentTasks: TaskDTO[];
};

export interface ExecutionRepository {
  list(params: ListExecutionsParams): Promise<{ executions: Execution[]; totalCount: number }>;
  fetchLogs(projectId: string, executionId: string): Promise<ExecutionLogs>;
  fetchThreadData(projectId: string, executionId: string): Promise<Record<string, string>>;
  getExecutionTasks(projectId: string, executionId: string): Promise<GetExecutionTasksResponse>;
  fetchStages(): Promise<{ id: string; title: string }[]>;
}
